<template>
  <div name="PostArticle" class="PostArticle-wrap">
    <el-row style="margin: 10px 0 50px 50px" type="flex">
      <font class="title"
        >上传文章&emsp;<span>您的分享是本站最本质的意义</span></font
      >
    </el-row>

    <el-form :model="ruleForm" :rules="rules" ref="ruleForm">
      <div class="forms">
        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font>是否为转载文章？</font>
          <div
            style="display: flex; width: 400px; justify-content: space-between"
          >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="1"
              border
              >是</el-radio
            >
            <el-radio
              style="width: 100px"
              v-model="ruleForm.article_status"
              label="2"
              border
              >否</el-radio
            >
          </div>
        </el-row>

        <el-row
          v-if="ruleForm.article_status == 1"
          type="flex"
          justify="space-between"
          style="width: 600px"
        >
          <font><span>*&nbsp;</span>转载网址：</font>
          <el-form-item prop="article_link">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_link"
              placeholder="请输入发表本文的原网址(转载其他网站的文章需填入)"
              clearable
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>*&nbsp;</span>文章标题：</font>
          <el-form-item prop="article_title">
            <el-input
              style="width: 400px; height: 50px"
              class="public-uncheck"
              v-model="ruleForm.article_title"
              placeholder="请输入文章标题"
              clearable
              :maxlength="20"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 600px">
          <font><span>*&nbsp;</span>文章所属专业：</font>
          <el-cascader
            style="width: 400px; height: 70px"
            v-model="ruleForm.article_major"
            placeholder="请选择文章所属的专业类别"
            :options="options"
            :props="{ expandTrigger: 'hover' }"
            :value="value"
          ></el-cascader>
        </el-row>

        <el-row
          type="flex"
          justify="space-between"
          style="width: 600px; margin-bottom: 50px"
        >
          <font style="margin: 0 0 0 13px">文章标签：</font>
          <el-select
            v-model="ruleForm.article_labels"
            ref="labelSelect"
            multiple
            filterable
            allow-create
            default-first-option
            placeholder="请输入文章标签(自定义)"
            style="width: 400px"
            @input.native="filterData"
            @change="bindChange"
          >
            <el-option
              v-for="item in optionsSubject"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-row>

        <el-row type="flex" justify="space-between" style="width: 900px">
          <font><span>*&nbsp;</span>文章内容简介：</font>
          <el-form-item prop="article_introduce">
            <el-input
              type="textarea"
              style="width: 700px"
              class="public-uncheck"
              v-model="ruleForm.article_introduce"
              placeholder="请简短的介绍一下你的文章内容(如果文章标题已经概括，那么直接用标题也可)"
              clearable
              :autosize="{ minRows: 2 }"
              :maxlength="100"
              show-word-limit
            ></el-input>
          </el-form-item>
        </el-row>

        <el-row>
          <el-col :span="0.5">
            <font><span>*&nbsp;</span>正文内容：</font>
          </el-col>
        </el-row>

        <el-row type="flex" justify="space-between">
          <el-form-item prop="article_details">
            <mavon-editor
              style="min-height: 500px; max-height: 800px; width: 1100px"
              ref="md"
              @change="change"
              @imgAdd="imgAdd"
              v-model="ruleForm.article_details"
            />
          </el-form-item>
        </el-row>

        <el-row type="flex" justify="end" style="margin: 30px 60px 0 0">
          <div class="submit" @click="submitArticle()">
            <font>提交文章</font>
          </div>
        </el-row>
      </div>
    </el-form>
  </div>
</template>

<script>
import { postArticleAPI, addArticleLabelAPI } from "@/api/articles";
import { uploadImageAPI } from "@/api/other";
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  name: "PostArticle",
  data() {
    return {
      optionsSubject: [],
      value: [],
      options: [
        {
          value: "文学",
          label: "文学",
          children: [
            {
              value: "中国语言文学类",
              label: "中国语言文学类",
            },
            {
              value: "新闻传播学类",
              label: "新闻传播学类",
            },
            {
              value: "外国语言文学类",
              label: "外国语言文学类",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "艺术学",
          label: "艺术学",
          children: [
            {
              value: "音乐",
              label: "音乐",
            },
            {
              value: "舞蹈",
              label: "舞蹈",
            },
            {
              value: "影视学类",
              label: "影视学类",
            },
            {
              value: "戏剧学类",
              label: "戏剧学类",
            },
            {
              value: "美术学类",
              label: "美术学类",
            },
            {
              value: "设计学类",
              label: "设计学类",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "理学",
          label: "理学",
          children: [
            {
              value: "信息与计算科学",
              label: "信息与计算科学",
            },
            {
              value: "数学与应用数学",
              label: "数学与应用数学",
            },
            {
              value: "应用化学",
              label: "应用化学",
            },
            {
              value: "生物技术",
              label: "生物技术",
            },
            {
              value: "生物科学",
              label: "生物科学",
            },
            {
              value: "应用物理学",
              label: "应用物理学",
            },
            {
              value: "应用心理学",
              label: "应用心理学",
            },
            {
              value: "统计学",
              label: "统计学",
            },
            {
              value: "化学",
              label: "化学",
            },
            {
              value: "物理学",
              label: "物理学",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "工学",
          label: "工学",
          children: [
            {
              value: "土木工程",
              label: "土木工程",
            },
            {
              value: "机械类专业",
              label: "机械类专业",
            },
            {
              value: "电气工程及其智能化",
              label: "电气工程及其智能化",
            },
            {
              value: "计算机科学与技术",
              label: "计算机科学与技术",
            },
            {
              value: "车辆工程",
              label: "车辆工程",
            },
            {
              value: "软件工程",
              label: "软件工程",
            },
            {
              value: "交通运输工程",
              label: "交通运输工程",
            },
            {
              value: "自动化",
              label: "自动化",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "农学",
          label: "农学",
          children: [
            {
              value: "园艺",
              label: "园艺",
            },
            {
              value: "种子科学与工程",
              label: "种子科学与工程",
            },
            {
              value: "设施农业科学与工程",
              label: "设施农业科学与工程",
            },
            {
              value: "农业资源与环境",
              label: "农业资源与环境",
            },
            {
              value: "动物科学",
              label: "动物科学",
            },
            {
              value: "林学",
              label: "林学",
            },
            {
              value: "水产养殖学",
              label: "水产养殖学",
            },
            {
              value: "草业学科",
              label: "草业学科",
            },
            {
              value: "茶学",
              label: "茶学",
            },
            {
              value: "烟草",
              label: "烟草",
            },
            {
              value: "农艺教育",
              label: "农艺教育",
            },
            {
              value: "园艺教育",
              label: "园艺教育",
            },
            {
              value: "其他类别",
              label: "其他类别",
            },
          ],
        },
        {
          value: "其他学科",
          label: "其他学科",
        },
      ],
      ruleForm: {
        // 文章信息
        article_status: "1", //默认为转载，1-转载，2-原创
        article_link: "",
        article_title: "",
        article_major: [],
        article_labels: [],
        article_introduce: "",
        article_details: "",
        article_md: "",
        article_uploaddate: "",
        article_updatedate: "",
        // 作者id
        author_id: localStorage.getItem("user_info")
          ? JSON.parse(localStorage.getItem("user_info")).id
          : null,
      },
      html: "", // 通过markdown及时转的html
      rules: {
        article_title: [
          { required: true, message: "请输入文章标题", trigger: "blur" },
        ],
        article_details: [
          { required: true, message: "请输入文章详细内容", trigger: "blur" },
        ],
        article_introduce: [
          {
            required: true,
            message: "请简要写一下这篇文章的简介",
            trigger: "blur",
          },
        ],
        article_link: [
          {
            required: true,
            message: "转载文章请务必填写原文地址！",
            trigger: "blur",
          },
        ],
      },
    };
  },
  computed: {
    presentDate() {
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      return year + "-" + this.addZero(month) + "-" + this.addZero(day);
    },
  },
  methods: {
    bindChange(e) {
      if (e.length > 5) {
        this.$message.warning("最多只能添加五个文章标签哦~");
        this.ruleForm.article_labels.splice(-1);
      }
    },
    addZero(s) {
      return s < 10 ? "0" + s : s;
    },
    filterData() {
      var str = this.$refs.labelSelect.$data.selectedLabel; // 此属性得到输入的文字
      if (str.length > 20) {
        this.$refs.labelSelect.$data.selectedLabel = str.substr(0, 21);
      }
    },
    // js压缩base64函数
    dealImage(base64, w, callback) {
      var newImage = new Image();
      var quality = 0.9; //压缩系数0-1之间
      newImage.src = base64;
      newImage.setAttribute("crossOrigin", "Anonymous"); //url为外域时需要
      var imgWidth, imgHeight;
      newImage.onload = function () {
        imgWidth = this.width;
        imgHeight = this.height;
        var canvas = document.createElement("canvas");
        var ctx = canvas.getContext("2d");
        if (Math.max(imgWidth, imgHeight) > w) {
          canvas.width = w;
          canvas.height = (w * imgHeight) / imgWidth;
        } else {
          canvas.width = imgWidth;
          canvas.height = imgHeight;
          quality = 0.9;
        }
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(this, 0, 0, canvas.width, canvas.height);
        var base64 = canvas.toDataURL("image/jpeg", quality); //压缩语句
        // // 如想确保图片压缩到自己想要的尺寸,如要求在50-150kb之间，请加以下语句，quality初始值根据情况自定
        // while (base64.length / 1024 > 150) {
        //   quality -= 0.01;
        //   base64 = canvas.toDataURL("image/jpeg", quality);
        // }
        // // 防止最后一次压缩低于最低尺寸，只要quality递减合理，无需考虑
        // while (base64.length / 1024 < 50) {
        //   quality += 0.001;
        //   base64 = canvas.toDataURL("image/jpeg", quality);
        // }
        callback(base64); //必须通过回调函数返回，否则无法及时拿到该值
      };
    },
    // 所有操作都会被解析重新渲染
    change(_, render) {
      // render 为 markdown 解析后的结果[html]
      this.html = render;
    },
    imgAdd(pos, file) {
      this.dealImage(file.miniurl, 800, (results) => {
        uploadImageAPI({ imgData: results }).then((res) => {
          console.log(res.data.base64Data);
          if (res.data.result_code === 0) {
            this.$refs.md.$img2Url(pos, res.data.imgURL);
          }
        });
      });
    },
    submitArticle() {
      if (
        (this.ruleForm.article_status == "1" &&
          this.ruleForm.article_link &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details) ||
        (this.ruleForm.article_status == "2" &&
          this.ruleForm.article_title &&
          this.ruleForm.article_major &&
          this.ruleForm.article_introduce &&
          this.ruleForm.article_details)
      ) {
        // 统一处理ruleForm
        this.ruleForm.article_md = this.ruleForm.article_details;
        this.ruleForm.article_details = this.html;
        this.ruleForm.article_uploaddate = this.presentDate;
        this.ruleForm.article_updatedate = this.presentDate;
        // 若检测到用户输入的标签还未存储，则将新标签加入本地
        let article_labels = [];
        if (localStorage.getItem("article_labels")) {
          for (
            var i = 0;
            i < JSON.parse(localStorage.getItem("article_labels")).length;
            i++
          ) {
            article_labels.push(
              JSON.parse(localStorage.getItem("article_labels"))[i]
            );
          }
        }
        let same = [];
        article_labels.forEach((item1) => {
          this.ruleForm.article_labels.forEach((item2) => {
            if (item1.value == item2) {
              console.log(item2);
              same.push(item2);
            }
          });
        });
        if (same.length != 0) {
          // 筛选不同词
          let result = this.ruleForm.article_labels.filter((item1) => {
            return same.every((item2) => {
              return item1 != item2;
            });
          });
          // result就是筛选好的不同词
          result.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
          });
          // 将不同词提交给后端数据库
          result.forEach((item) => {
            addArticleLabelAPI({ label_name: item }).then((res) => {
              console.log(res.data);
            });
          });
        } else {
          this.ruleForm.article_labels.forEach((item) => {
            article_labels.push({
              label: item,
              value: item,
            });
            addArticleLabelAPI({ label_name: item }).then((res) => {
              console.log(res.data);
            });
          });
        }
        localStorage.setItem("article_labels", JSON.stringify(article_labels));
        console.log(this.ruleForm);
        postArticleAPI(this.ruleForm).then((res) => {
          console.log(res.data);
          if (localStorage.getItem("not_saved_article_info")) {
            localStorage.removeItem("not_saved_article_info");
          }
        });

        // 将表单初始化
        this.ruleForm = {
          article_link: "",
          article_title: "",
          article_major: [],
          article_labels: [],
          article_introduce: "",
          article_details: "",
          article_md: "",
          article_uploaddate: "",
          article_updatedate: "",
          author_id: localStorage.getItem("user_info")
            ? JSON.parse(localStorage.getItem("user_info")).id
            : null,
        };
        this.$notify({
          title: "发布成功！",
          message: "快快前往首页看看吧！",
          type: "success",
        });
      } else {
        this.$notify({
          title: "发布文章失败！",
          message: "必填项未填满！",
          type: "error",
        });
      }
    },
  },
  watch: {
    ruleForm: {
      handler: function (newV, _) {
        localStorage.setItem("not_saved_article_info", JSON.stringify(newV));
      },
      deep: true,
    },
  },
  mounted() {
    if (localStorage.getItem("not_saved_article_info")) {
      this.$confirm("是否继续上次编辑？", "上次编辑内容未发布", {
        distinguishCancelAndClose: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      })
        .then(() => {
          this.ruleForm = {
            ...JSON.parse(localStorage.getItem("not_saved_article_info")),
          };
        })
        .catch(() => {
          localStorage.removeItem("not_saved_article_info");
        });
    }
    if (localStorage.getItem("article_labels")) {
      for (
        var i = 0;
        i < JSON.parse(localStorage.getItem("article_labels")).length;
        i++
      ) {
        this.optionsSubject.push(
          JSON.parse(localStorage.getItem("article_labels"))[i]
        );
      }
    }
  },
  components: { mavonEditor },
};
</script>
<style scoped>
.PostArticle-wrap {
  margin-bottom: 30px;
  padding: 20px;
  width: 1300px;
  border-radius: 5px;
  background: #ffffff;
}
.title {
  font-family: SourceHanSansCN-Bold;
  font-size: 36px;
  font-weight: bold;
  color: #3d3d3d;
}
.title span {
  font-family: SourceHanSansCN-Bold;
  font-size: 16px;
  font-weight: normal;
  color: #9e9e9e;
}
.forms {
  margin: 0 0 0 100px;
}
.forms > * {
  margin: 20px 0 20px 0;
}
.forms > * font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  color: #3d3d3d;
}
.forms > * font span {
  color: #ff8200;
}
.submit {
  width: 150px;
  height: 60px;
  border-radius: 30px;
  background: #76fff5;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s;
}
.submit font {
  font-family: SourceHanSansCN-Regular;
  font-size: 18px;
  font-weight: normal;
  line-height: 18px;
  color: #3d3d3d;
  transition: all 0.5s;
}
.submit:hover {
  background-color: #00ead8;
}
.submit:hover font {
  color: #ffffff;
}
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
::v-deep .el-textarea__inner {
  display: block;
  resize: vertical;
  padding: 5px 15px;
  line-height: 1.5;
  box-sizing: border-box;
  width: 100%;
  font-size: inherit;
  color: #606266;
  background-color: #ffffff;
  background-image: none;
  border: 1px solid #dcdfe6;
  transition: border 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  border-radius: 5px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
</style>