<template>
  <div class="index">
    <el-row type="flex" justify="center">
      <PostArticleVue />
    </el-row>
  </div>
</template>

<script>
import PostArticleVue from "@/components/ModelPost/PostArticle.vue";
export default {
  name: "index-post",
  components: { PostArticleVue },
  beforeRouteLeave(to, from, next) {
    if (!localStorage.getItem("not_saved_article_info")) {
      next(true);
    } else {
      this.$confirm(
        "检测到未保存的内容，离开页面后会保存未发布内容",
        "确认离开此页面？",
        {
          distinguishCancelAndClose: true,
          confirmButtonText: "确认",
          cancelButtonText: "取消",
        }
      )
        .then(() => {
          next(true);
        })
        .catch(() => {
          next(false);
        });
    }
  },
};
</script>

<style scoped>
.index {
  width: 100%;
}
</style>